import { createRouter, createWebHashHistory } from 'vue-router';
import Home from '../views/Home.vue';
import Category from '../views/Category.vue';
import Viewer from '../views/viewer.vue';

const channel = new BroadcastChannel('route-updated');
const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home
	},
	{
		path: '/category',
		name: 'Category',
		component: Category
	},
	{
		path: '/view',
		name: 'Viewer',
		component: Viewer
	},
	{
		path: '/about',
		name: 'AboutSite',
		component: function () {
			return import('../views/about/site.vue')
		}
	},
	{"path":"/chinesecolor","name":"chinesecolor","component":function(){return import("../pages/chinesecolor.vue")}},{"path":"/japanesecolor","name":"japanesecolor","component":function(){return import("../pages/japanesecolor.vue")}},{"path":"/latex","name":"latex","component":function(){return import("../pages/latex.vue")}},{"path":"/markup","name":"markup","component":function(){return import("../pages/markup.vue")}},{"path":"/entertain/ballcrush","name":"ballcrush","component":function(){return import("../pages/entertain/ballcrush.vue")}},{"path":"/entertain/rushgo","name":"rushgo","component":function(){return import("../pages/entertain/rushgo.vue")}},{"path":"/entertain/training","name":"training","component":function(){return import("../pages/entertain/training.vue")}},{"path":"/popsci/quantum1","name":"quantum1","component":function(){return import("../pages/popsci/quantum1.vue")}},{"path":"/tools/markupEditor","name":"markupEditor","component":function(){return import("../pages/tools/markupEditor.vue")}},{"path":"/tools/parseMarkup","name":"parseMarkup","component":function(){return import("../pages/tools/parseMarkup.vue")}},{path:'/aboutMe',name:'AboutMe',component:function(){return import('../views/aboutMe.vue')}},
	{
		path: '/404',
		name: 'PageNotFound',
		component: function () {
			return import('../views/404.vue')
		}
	},
	{
		path: '/:pathMatch(.*)*',
		name: 'PageNotFound',
		component: function () {
			return import('../views/404.vue')
		}
	}
];
const router = createRouter({
	history: createWebHashHistory(),
	routes
});
router.afterEach((to, from) => {
	if (to.name === 'Category') {
		let list = getPathNameList(to.query.c.split(','), false);
		if (!list) {
			document.title = router.app.config.globalProperties.SiteName;
		}
		else {
			document.title = router.app.config.globalProperties.SiteName + ' / ' + list.map(c => c.name).join(' / ');
		}
	}
	else {
		document.title = router.app.config.globalProperties.SiteName;
	}
	channel.postMessage({
		from: from.fullPath,
		to: to.fullPath
	});

	var app = document.querySelector('#app');
	app.scrollTo(0, 0);
});

export default router;